import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import styled from 'styled-components'
import { ButtonRound, ErrorText } from '../stylesheets/mainStyled'
import { device } from '../stylesheets/device'
import { ReactComponent as CloseIcon } from '../images/close.svg'
import { ReactComponent as CheckIcon } from '../images/check.svg'
import TextInput from './FormikInputs/TextInput'
import MaskInput from './FormikInputs/MaskInput'
import SelectInput from './FormikInputs/SelectInput'
import { addMember } from '../services/apiMethods'
import { getAuth } from '../redux/global/globalSelectors'
import { addMemberId, staffError, staffLoader } from '../redux/staff/staffActions'
import { getError, getLoader } from '../redux/staff/staffSelectors'
import Loading from './Loader'

const AddMember = ({ onClose }) => {
  const dispatch = useDispatch()
  const auth = useSelector(getAuth)
  const loader = useSelector(getLoader)
  const error = useSelector(getError)
  return (
    <Container>
      <Header>
        <Title> </Title>
        <CloseIcon onClick={onClose} />
      </Header>
      <Formik
        initialValues={{
          surname: '',
          name: '',
          phone: '',
          memberType: '',
        }}
        validationSchema={Yup.object({
          surname: Yup.string().required('is required'),
          name: Yup.string().required('is required'),
          phone: Yup.string()
            .test(
              'len',
              '   ',
              val => val?.replace(/[^0-9]/g, '').length === 12
            )
            .required('is required'),
          memberType: Yup.string().required('is required'),
        })}
        onSubmit={async ({ surname, name, phone, memberType }) => {
          const member = {
            surname,
            name,
            phone: phone.replace(/[^0-9]/g, ''),
            memberType,
          }
          dispatch(staffLoader())
          try {
            const { data } = await addMember(member, auth)
            dispatch(addMemberId(data))
            onClose()
          } catch (error) {
            dispatch(staffError(error))
          }
        }}
      >
        <Form>
          <FormBox>
            <TextInput label="" name="surname" type="text" autoComplete="none" />
            <TextInput label="" name="name" type="text" autoComplete="none" />
            <MaskInput
              label=" "
              name="phone"
              type="text"
              mask="+38(999) 999-99-99"
            />
            <SelectInput label="" name="memberType">
              <option value="" hidden></option>
              <option value="DRIVER"></option>
              <option value="STEWARD"></option>
              <option value="DISPATCHER"></option>
            </SelectInput>
            {error && <ErrorText>{error.message}</ErrorText>}
            <ButtonRound type="submit">
              {loader ? (
                <Loading type="ThreeDots" color="#00BFFF" height={15} width={40} />
              ) : (
                <CheckIcon />
              )}
            </ButtonRound>
          </FormBox>
        </Form>
      </Formik>
    </Container>
  )
}

export default AddMember

const Container = styled.div`
  background-color: #fff;
  width: 100%;
  height: 100%;
  position: relative;

  @media ${device.tablet} {
    width: 320px;
    margin: 0 auto;
  }
`

const Header = styled.div`
  padding: 12.4px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
`
const Title = styled.p`
  font-size: 24px;
`
const FormBox = styled.div`
  padding: 20px;
`
