import { Types } from '../actionTypes'

export const setStaff = data => ({ type: Types.GET_STAFF, payload: data })

export const setMember = data => ({ type: Types.GET_MEMBER, payload: data })

export const editMemberId = data => ({ type: Types.EDIT_MEMBER, payload: data })

export const addMemberId = data => ({ type: Types.ADD_MEMBER, payload: data })

export const removeMemberId = id => ({ type: Types.REMOVE_MEMBER, payload: id })

export const staffLoader = () => ({ type: Types.START_STAFF_LOADER })

export const staffError = err => ({ type: Types.STAFF_ERROR, payload: err })
